import { mapActions, mapGetters } from 'vuex'

export default {
  async fetch() {
    try {
      await this.GET_LATEST()
    } catch (e) {
      console.log(e)
    }
    this.current = this.LATEST[0]
  },
  data() {
    return {
      current: {},
    }
  },
  computed: {
    ...mapGetters({
      LATEST: 'products/LATEST',
    }),
  },
  methods: {
    ...mapActions({
      GET_LATEST: 'products/GET_LATEST',
    }),
    changeDescription(index) {
      this.current = this.LATEST[index]
    },
  },
}
